<template>
    <div class="m-sale-car">
        <v-header>我要卖车</v-header>
        <div class="banner"><img :src="imgUrl" alt=""></div>
         <div class="weui-cells weui-cells_form">
            <div class="weui-cell">
                <div class="weui-cell__hd"><label class="weui-label"><span class="require">*</span>联系方式</label></div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="number" pattern="[0-9]*" placeholder="请输入手机号"/>
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label"><span class="require">*</span>姓名</label>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="tel" placeholder="请输入姓名"/>
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label"><span class="require">*</span>车辆描述</label>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="tel" placeholder="如：14款奥迪A3，2万公里"/>
                </div>
            </div>
        </div>
        <div class="weui-btn-area">
            <a class="weui-btn weui-btn_submit" href="javascript:" id="submit">提交</a>
        </div>
    </div>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
export default {
    data(){
        return{
            imgUrl:'',
        }
    },
    components:{
        vHeader
    },

    mounted(){
        let deviceRatio = window.devicePixelRatio;
        if(deviceRatio == 1){
            this.imgUrl = "static/images/banner.png"
        }else if(deviceRatio == 2){
            this.imgUrl = 'static/images/banner@2x.png'
        }else{
            this.imgUrl = 'static/images/banner@3x.png'
        }
    }   
}
</script>



